.card {
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    border-radius: 10px;
    margin-top: 1em;
    cursor: pointer;
    overflow: hidden;
  }
  
  img {
    border-radius: 10px;
  }

.card:hover {
    box-shadow: 0 8px 16px 0 black;
  }
  
  .container {
    padding: 2px 16px;
  }